import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import {HttpService} from '../http.service';


@Component({
  selector: 'app-aircraft',
  templateUrl: './aircraft.component.html',
  styleUrls: ['./aircraft.component.css']
})
export class AircraftComponent implements OnInit {
  modalRef: BsModalRef;
  flightData:any = [];
  aircraftData:any = [];
  storeFIlterName: any[];
  rowsLogs: any;
  MSN = '';
  Name = '';
  HarnessLength;
  GrossWeight;
  AtmosphericPressure;
  FuelCapacity_left;
  FuelCapacity_right;
  FuelQuantity_left;
  FuelQuantity_right;
  Max_Altitude;
  Flight_No;
  constructor(private modalService: BsModalService, private _http:HttpService) { }

  ngOnInit() {
    this._http.getAllFlights().toPromise().then(result => {
      this.flightData = result;
      this.rowsLogs = result;
      console.log(this.flightData);
    }).catch(err => {
      console.log(err);
    })
    this._http.getAllCrafts().toPromise().then(result => {
      this.aircraftData = result;
    }).catch(err => {
      console.log(err);
    })
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, {class: 'modal-lg'});
  }

  openForm(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, {class: 'modal-lg'});
  }

  openCraftModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, {class: 'modal-lg'});
  }

  submitForm() {
    const data = {
      MSN:this.MSN,
      Name:this.Name
    }
    this._http.submitCraft(data).toPromise().then(result => {
      console.log(result);
      alert('Aircraft registered successfully')
    }).catch(err => {
      console.log(err);
      alert('Some error occured');
    })
  }

  searchPeople(e) {
    console.log(e);
    this.flightData = this.rowsLogs;
    this.storeFIlterName = [];
    const q = e;

    if (e !== null) {
      for (const plane of this.flightData) {
        if (plane.Flight_No.includes(q) || plane.MSN.Name.includes(q)) {
          this.storeFIlterName.push(plane);
        }
      }
    } else {
      this.storeFIlterName = this.rowsLogs;
    }

    this.flightData = this.storeFIlterName;
  }

 submitFlight() {
  const data = {
   MSN:this.MSN,
  Name:this.Name,
  HarnessLength:this.HarnessLength,
  GrossWeight:this.GrossWeight,
  AtmosphericPressure:this.AtmosphericPressure,
  FuelCapacity_left:this.FuelCapacity_left,
  FuelCapacity_right:this.FuelCapacity_right,
  FuelQuantity_left:this.FuelQuantity_left,
  FuelQuantity_right:this.FuelQuantity_right,
  Max_Altitude:this.Max_Altitude,
  Flight_No:this.Flight_No
  }
  this._http.submitFlightData(data).toPromise().then(result => {
    alert('flight registered successfully')
  }).catch(err => {
    console.log(err);
  })
 }
}
