import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  baseUrl = 'http://airbuscms.herokuapp.com/api';
  constructor(private http:HttpClient) { }

  getAllFlights() {
    return this.http.get(`${this.baseUrl}/flight/`);
  }

  getAllCrafts() {
    return this.http.get(`${this.baseUrl}/aircraft/`);
  }

  submitCraft(data) {
    return this.http.post(`${this.baseUrl}/aircraft/`, data);
  }

  getNews() {
    return this.http.get(`${this.baseUrl}/news/`);
  }

  submitFlightData(data) {
    return this.http.post(`${this.baseUrl}flight`,data);
  }
}
